/**
 * Utilitaires SEO - G√©n√©ration metadata dynamique
 */
import { Metadata } from 'next';

const SITE_URL = process.env.NEXT_PUBLIC_SITE_URL || 'http://localhost:3000';
const SITE_NAME = 'ZenFlow';
const DEFAULT_DESCRIPTION = 'Boutique en ligne de produits cr√©atifs et artisanaux. D√©couvrez notre s√©lection unique de produits faits main.';

export interface ProductMetadata {
  title: string;
  description: string;
  price: number;
  image: string;
  category?: string;
  inStock?: boolean;
  url?: string;
}

export interface PageMetadata {
  title: string;
  description?: string;
  image?: string;
  type?: 'website' | 'product' | 'article';
  noindex?: boolean;
  path?: string;
}

/**
 * G√©n√®re metadata compl√®te pour une page
 */
export function generateMetadata(page: PageMetadata): Metadata {
  const title = page.title.includes(SITE_NAME)
    ? page.title
    : `${page.title} | ${SITE_NAME}`;

  const description = page.description || DEFAULT_DESCRIPTION;
  const image = page.image || `${SITE_URL}/og-image.jpg`;
  const canonicalUrl = `${SITE_URL.replace(/\/$/, '')}${page.path ? `/${page.path.replace(/^\//, '')}` : ''}`;

  return {
    title,
    description,
    keywords: ['e-commerce', 'boutique en ligne', 'produits cr√©atifs', 'artisanat'],
    authors: [{ name: SITE_NAME }],
    creator: SITE_NAME,
    publisher: SITE_NAME,
    robots: page.noindex ? 'noindex,nofollow' : 'index,follow',
    openGraph: {
      type: (page.type === 'product' ? 'website' : page.type) || 'website',
      locale: 'fr_FR',
      url: canonicalUrl,
      siteName: SITE_NAME,
      title,
      description,
      images: [
        {
          url: image,
          width: 1200,
          height: 630,
          alt: title,
        },
      ],
    },
    twitter: {
      card: 'summary_large_image',
      title,
      description,
      images: [image],
      creator: '@zenflow',
    },
    alternates: {
      canonical: canonicalUrl,
    },
    other: {
      'theme-color': '#6366f1', // primary-500 (indigo)
    },
  };
}

/**
 * G√©n√®re metadata pour une page produit
 */
export function generateProductMetadata(product: ProductMetadata): Metadata {
  const title = `${product.title} | ${SITE_NAME}`;
  const description = product.description || `D√©couvrez ${product.title} sur ${SITE_NAME}. Prix: ${product.price}‚Ç¨`;
  const fallbackSlug = product.title.toLowerCase().replace(/\s+/g, '-');
  const canonicalUrl =
    product.url || `${SITE_URL.replace(/\/$/, '')}/products/${fallbackSlug}`;

  return {
    title,
    description,
    keywords: [
      product.title,
      product.category || '',
      'achat en ligne',
      'livraison rapide',
    ].filter(Boolean),
    openGraph: {
      type: 'website', // Next.js ne supporte que 'website' | 'article'
      locale: 'fr_FR',
      url: canonicalUrl,
      siteName: SITE_NAME,
      title,
      description,
      images: [
        {
          url: product.image,
          width: 1200,
          height: 630,
          alt: product.title,
        },
      ],
    },
    twitter: {
      card: 'summary_large_image',
      title,
      description,
      images: [product.image],
    },
    alternates: {
      canonical: canonicalUrl,
    },
  };
}

/**
 * G√©n√®re JSON-LD structured data pour un produit
 */
export function generateProductStructuredData(product: ProductMetadata & { id: string; url: string }) {
  return {
    '@context': 'https://schema.org',
    '@type': 'Product',
    name: product.title,
    description: product.description,
    image: product.image,
    sku: product.id,
    category: product.category,
    brand: {
      '@type': 'Brand',
      name: SITE_NAME,
    },
    offers: {
      '@type': 'Offer',
      url: product.url,
      priceCurrency: 'EUR',
      price: product.price.toString(),
      availability: product.inStock
        ? 'https://schema.org/InStock'
        : 'https://schema.org/OutOfStock',
      seller: {
        '@type': 'Organization',
        name: SITE_NAME,
      },
    },
    aggregateRating: {
      '@type': 'AggregateRating',
      ratingValue: '4.5',
      reviewCount: '10',
    },
  };
}

/**
 * G√©n√®re JSON-LD pour une organisation
 */
export function generateOrganizationStructuredData() {
  return {
    '@context': 'https://schema.org',
    '@type': 'Organization',
    name: SITE_NAME,
    url: SITE_URL,
    logo: `${SITE_URL}/logo.png`,
    contactPoint: {
      '@type': 'ContactPoint',
      contactType: 'Customer Service',
      email: 'contact@zenflow.com',
    },
    sameAs: [
      'https://www.facebook.com/zenflow',
      'https://www.instagram.com/zenflow',
      'https://www.twitter.com/zenflow',
    ],
  };
}

/**
 * G√©n√®re JSON-LD pour un breadcrumb
 */
export function generateBreadcrumbStructuredData(items: Array<{ name: string; url: string }>) {
  return {
    '@context': 'https://schema.org',
    '@type': 'BreadcrumbList',
    itemListElement: items.map((item, index) => ({
      '@type': 'ListItem',
      position: index + 1,
      name: item.name,
      item: item.url,
    })),
  };
}
