// app/layout.tsx (Critical Preload + Fonts Optimization)
import { AnalyticsInit } from '@/components/AnalyticsInit';
import { AttributionCapture } from '@/components/AttributionCapture';
import { ErrorBoundary } from '@/components/ErrorBoundary';
import { Footer } from '@/components/Footer';
import { Navigation } from '@/components/Navigation';
import { StructuredData } from '@/components/StructuredData';
import { ToastContainer } from '@/components/Toast';
import { DarkModeProvider } from '@/contexts/DarkModeContext';
import { SWRProvider } from '@/lib/providers/SWRProvider';
import { generateOrganizationStructuredData } from '@/lib/seo/metadata';
import type { Metadata, Viewport } from 'next';
import dynamic from 'next/dynamic';
import { Inter } from 'next/font/google';
import './globals.css';

// Lazy load components non-critiques pour am√©liorer le First Contentful Paint
const Chatbot = dynamic(() => import('@/components/Chatbot'), {
  ssr: false, // Chatbot n'a pas besoin de SSR
});
const ScrollToTop = dynamic(() => import('@/components/ScrollToTop').then(mod => ({ default: mod.ScrollToTop })), {
  ssr: false,
});
const PWAInstallPrompt = dynamic(() => import('@/components/PWAInstallPrompt').then(mod => ({ default: mod.PWAInstallPrompt })), {
  ssr: false,
});

const inter = Inter({
  subsets: ['latin'],
  display: 'swap',
  preload: true,
  variable: '--font-inter'
});

const siteUrl = process.env.NEXT_PUBLIC_SITE_URL || 'http://localhost:3002';

export const metadata: Metadata = {
  title: {
    default: 'ZenFlow - Technologie de Demain',
    template: '%s | ZenFlow',
  },
  description: 'Smartphones dernier cri, accessoires intelligents et solutions domotiques r√©volutionnaires. Innovation et design au service de votre quotidien.',
  metadataBase: new URL(siteUrl),
  keywords: ['smartphones', 'accessoires', 'domotique', 'technologie', 'e-commerce', 'produits innovants'],
  authors: [{ name: 'ZenFlow' }],
  creator: 'ZenFlow',
  publisher: 'ZenFlow',
  formatDetection: {
    email: false,
    address: false,
    telephone: false,
  },
  openGraph: {
    type: 'website',
    locale: 'fr_FR',
    url: siteUrl,
    siteName: 'ZenFlow',
    title: 'ZenFlow - Technologie de Demain',
    description: 'Smartphones dernier cri, accessoires intelligents et solutions domotiques r√©volutionnaires. Innovation et design au service de votre quotidien.',
    images: [
      {
        url: `${siteUrl}/og-image.jpg`,
        width: 1200,
        height: 630,
        alt: 'ZenFlow - Votre boutique en ligne',
      },
    ],
  },
  twitter: {
    card: 'summary_large_image',
    title: 'ZenFlow - Technologie de Demain',
    description: 'Smartphones dernier cri, accessoires intelligents et solutions domotiques r√©volutionnaires.',
    images: [`${siteUrl}/og-image.jpg`],
    creator: '@zenflow',
  },
  robots: {
    index: true,
    follow: true,
    googleBot: {
      index: true,
      follow: true,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },
  other: {
    'X-UA-Compatible': 'IE=edge',
    'application-name': 'ZenFlow',
  },
};

export const viewport: Viewport = {
  width: 'device-width',
  initialScale: 1,
  themeColor: '#0a0a0a',
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const supabaseOrigin = (() => {
    const raw = process.env.NEXT_PUBLIC_SUPABASE_URL;
    if (!raw) return null;
    try {
      return new URL(raw).origin;
    } catch {
      return null;
    }
  })();

  return (
    <html lang="fr" className={`${inter.variable} font-sans`}>
      <head>
        {/* üöÄ Critical Preload PageSpeed 95+ */}
        {supabaseOrigin ? (
          <link rel="preconnect" href={supabaseOrigin} crossOrigin="anonymous" />
        ) : null}
        <link rel="preconnect" href="https://fonts.googleapis.com" />
        <link rel="preconnect" href="https://fonts.gstatic.com" crossOrigin="anonymous" />

        {/* DNS Prefetch */}
        <link rel="dns-prefetch" href="//supabase.co" />
        <link rel="dns-prefetch" href="//cloudflare.com" />

        {/* PWA */}
        <link rel="manifest" href="/manifest.json" />
      </head>
      <body
        className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 antialiased"
        suppressHydrationWarning
      >
        {/* Service Worker temporairement d√©sactiv√© */}
        {/* <ServiceWorkerRegistration /> */}
        <AnalyticsInit />
        <StructuredData data={generateOrganizationStructuredData()} />
        <AttributionCapture />
        <DarkModeProvider>
          <SWRProvider>
            <ErrorBoundary>
              <a
                href="#main-content"
                className="sr-only focus:not-sr-only focus:fixed focus:top-3 focus:left-3 focus:z-[60] focus:bg-white focus:text-gray-900 focus:px-4 focus:py-2 focus:rounded-md focus:shadow dark:focus:bg-gray-900 dark:focus:text-gray-100"
              >
                Aller au contenu principal
              </a>
              <Navigation />
              <main id="main-content">{children}</main>
              <Footer />
              <ScrollToTop />
              <ToastContainer />
              <PWAInstallPrompt />
              <Chatbot />
            </ErrorBoundary>
          </SWRProvider>
        </DarkModeProvider>
      </body>
    </html>
  );
}
